.timeline {
    position: relative;
    width: 100%;
    margin: 0 auto;

    &::before {
        content: "";
        position: absolute;
        width: 100%;
        top: 202px;
        left: 0;
        height: 1px;
        background-color: $gray-300;
    }
    .swiper-slide {
        .item-box {
            margin: 255px 0px 20px;
            background-color: transparent;
            &::before,
            &::after {
                content: "";
                position: absolute;
                left: 50%;
                background: $gray-300;
            }
            &.active {
                &::before,
                &::after {
                    content: "";
                    position: absolute;
                    left: 50%;
                    background: $primary
                }
                &::before {
                    width: 2px;
                    height: 55px;
                    top: -46px;
                    transform: translateX(-50%);
                }
    
                &::after {
                    width: 13px;
                    height: 13px;
                    top: -59px;
                    border-radius: 3px;
                    transform: translateX(-50%) rotate(45deg);
                }
                .roadmap-content {
                    border: 2px dashed $primary;
                }
            }

            &::before {
                width: 2px;
                height: 55px;
                top: -46px;
                transform: translateX(-50%);
            }

            &::after {
                width: 13px;
                height: 13px;
                top: -59px;
                border-radius: 3px;
                transform: translateX(-50%) rotate(45deg);
            }
        }
        .roadmap-content {
            min-height: 110px;
            display: flex;
            align-items: center;
            border: 2px dashed;
            border-color: transparent;
        }
        .active-line{
            position: relative;
            &::before {
                content: "";
                position: absolute;
                width: 100%;
                top: -61px;
                left: 50%;
                height: 2px;
                background-color: $primary;
    
            }
        }

        &:nth-child(even) {
            margin-top: 5px;
            transform: rotate(-180deg);

            .roadmap-content {
                transform: rotate(180deg);
            }
            .active-line{
                position: relative;
                &::before {
                    right: 50%; 
                    left: auto;
                    bottom: 59px;
                    top: auto;
                }
            }
        }
    }

    .swiper-button-next,
    .swiper-button-prev {
        height: 40px;
        width: 40px;
        line-height: 40px;
        border-radius: 50%;
        background-color: lighten($primary, 2.5%);

        &::after {
            font-size: 24px;
            color: $white;
        }
    }

    .swiper-button-next {
        right: 0;
        &::after {
            content: "\eb9f";
            font-family: unicons-line;
        }
    }

    .swiper-button-prev {
        left: 0;

        &::after {
            content: "\ec49";
            font-family: unicons-line;
        }
    }

    @media (max-width: 991.98px) { 
        &::before {
            top: 194px;
        }
        .swiper-slide {
            .item-box { 
                margin-top: 247px;
            }
        }
    }
}