.market-data-box {
    z-index: 2;

    .apexcharts-svg {
        z-index: 1;
    }
}

.market-data-menu {
    .nav-item {
        .nav-link {
            &.active {
                color: $primary;
                background-color: rgba($primary, 0.1);
            }
        }
    }
}

.market-table-title {
    table {
        font-size: 16px;

        th {
            color: $secondary;
            font-size: 15px;
            font-weight: 500;
            border-bottom: 0;
        }
    }
}



.hide {
    display: none;
    visibility: hidden;
    height: 0;
}

.icon {
    svg {
        fill: transparent;
    }
}

.favourite {
    &:checked {
        &~.btn-star svg {
            --color-svg: #da3746;
            color: $danger !important;
        }
    }
}

.market-toolbar {
    a {
        padding: 6px 8px;
        color: $text-muted;
        background-color: $light;
        margin-left: 5px;
        border-radius: 4px;
        font-size: 14px;
        font-weight: 500;
    }
}

body[data-layout-mode='dark'] {
    .market-table-title {
        table {
            th {
                color: $text-muted;
            }
        }
    }

    .market-toolbar {
        a {
            background-color: $gray-dark-300;
        }
    }
}