//
// menu.scss
//

.top-bar {
    padding: 10px 0;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 2;
    background-image: url('../images/top-bar.png');
    background-size: cover;
    color:$white;
    line-height: 1;
}

.navbar {
    transition: all 0.5s ease;
    padding: 0;
    top: 33px;
    background-color: transparent;

    .navbar-nav {
        .nav-link {
            color: $gray-700;
            font-size: 16px;
            background-color: transparent;
            padding: 24px 20px;
            transition: all 0.3s ease;
            position: relative;
            font-weight: 500;
            &:hover,
            &:active,
            &.active {
                color: $primary;
            }
        }
    }

    .navbar-toggler {
        padding: 0px;
        border: none;
        font-size: 24px;

        &:focus {
            box-shadow: none;
        }
    }
}

.header-item {
    height: 73px;
    display: inline-flex;
    align-items: center;
}

.language-switch{
    .btn {
        &:focus {
            box-shadow: none;
        }
    }
}

//nav-sticy
.nav-sticky {
    &.navbar {
        background: $white;
        margin-top: 0px;
        top: 0;
        box-shadow: 0px 3px 10px 0px rgba($dark, 0.08);
    }

    .header-item {
        color: $dark;
    }

    .navbar-brand {
        .logo-dark {
            display: inline-block;
        }

        .logo-light {
            display: none;
        }
    }
}

@media (max-width: 991.98px) {
    .navbar {
        .navbar-nav {
            .nav-item {
                .nav-link {
                    padding: 8px 15px;
                }
            }
        }
    }

    .navbar-collapse {
        max-height: 385px;
        overflow-y: auto;
        padding: 0;
        position: absolute;
        left: 0px;
        right: 0px;
        top: 68px;
        background-color: $white;
        padding-bottom: 10px;
        padding-left: 25px;
        box-shadow: 0px 3px 10px 0px rgba($dark, 0.08);
    }
}

.arrow-down {
    display: inline-block;

    &:after {
        border-color: initial;
        border-style: solid;
        border-width: 0 0 1px 1px;
        content: "";
        height: .4em;
        display: inline-block;
        right: 5px;
        top: 50%;
        margin-left: 10px;
        transform: rotate(-45deg) translateY(-50%);
        transform-origin: top;
        transition: all .3s ease-out;
        width: .4em;
    }

    @media(max-width: 991.98px) {
        &::after {
            right: 30px;
            position: absolute;
        }
    }
}

body[data-topbar='light'] {
    .top-bar {
        background-color: $light;
    }
    .navbar{ 
        background-color: $white;
    }
}

body[data-layout-mode='dark'] {
    .navbar {
        background-color: $gray-dark-200;
        .navbar-nav {
            .nav-link { 
                color: $gray-300;
            }
        }
    }
    .nav-sticky {
        &.navbar{  
            background-color: $gray-dark-200;
            box-shadow: 0px 3px 10px 0px rgba($gray-dark-100, 0.85);
        }  
    }
    @media (max-width: 991.98px) {
        .navbar-collapse { 
            background-color: $gray-dark-200;
            box-shadow: 0px 3px 10px 0px rgba($gray-dark-400, 0.08);
        }
    }
    .navbar-toggler {
        color: rgba($white, 0.25);
    }
}