.intro-play {
    a {
        display: inline-block;
        position: relative;
        width: 48px;
        height: 48px;
        line-height: 48px;
        border-radius: 50%;
        font-size: 20px;
        text-align: center;
        background-color: $primary;
        color: $white;
        z-index: 2;
    }
}

.bg-about-cta {
    padding: 95px 0 130px;

    .bg-overlay {
        background-color: rgba($dark, 0.90);
    }
}

#myVideo {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.testimonial-box {
    background: rgba($white, 0.12);
    backdrop-filter: blur(8px);
    border-color: transparent;
    color: $white;
    max-width: 810px;
    margin: 0 auto;
}