.bg-ico-contact {
    position: relative;
    margin-bottom: -25px;
    z-index: 3;
    padding-bottom: 0;
}


.contact-box{
    position: relative;
    padding: 30px 30px 30px 30px;
}

.contact-form{
    .form-control {
        border-bottom: 3px solid $gray-400;
    }
}

.contact-icon{
    width: 44px;
    height: 44px;
    line-height: 50px;
    text-align: center;
    background-color: $primary;
    color: $light;
    font-size: 20px;
}

.custom-form{
    position: relative;
    @media (min-width: 992px) { 
        margin-top: -215px;
    }
}