// Pagination rounded (Custom)
.pagination {
    .page-link {
        margin: 0 5px;
        border: none;
        width: 45px;
        height: 45px;
        padding: 0;
        text-align: center;
        line-height: 45px;
        border-radius: 5px;
        margin-right: 3px;
    }
}

body[data-layout-mode='dark'] {
    .page-link {
        background-color: $gray-dark-300;
    }
    .page-item {
        &.active {
            .page-link {
                background-color: $gray-dark-200;
            }
        }
    }
}