//
// page-title.scss
//

.page-title-box {
    position: relative;
    padding-top: 225px;
    padding-bottom: 145px;
    background-image: url('../images/home/page-title.png');
    background-position: center;
    background-size: cover;
    z-index: 1;
}

.lines {
    left: 0;
    right: 0;
    margin: auto;
    top: 0; 
    height: 100%;
    position: absolute;
    z-index: -1;
}

.lines {
    .line{
        width: 1px;
        top: 25px;
        height: 115%;
        overflow: hidden;
        position: absolute;
        background: rgba($white, 0.07);
        transform: rotate(45deg);
        &::after {
            left: 0;
            top: -50%;
            width: 100%;
            content: "";
            height: 25px;
            display: block;
            position: absolute;
            animation: run 8s infinite;
            background: rgba($white, 0.95);
        }
        &:nth-child(1) {
            left: 16.66%;
            &::after {
                animation-delay: 2s;
            }
        }
        &:nth-child(2) {
            left: 33.32%;
            &::after {
                animation-delay: 3s;
            }
        }
        &:nth-child(3) {
            left: 49.980000000000004%;
            &::after {
                animation-delay: 4s;
            }
        }
        &:nth-child(4) {
            left: 66.64%;
            &::after {
                animation-delay: 5s;
            }
        }
        &:nth-child(5) {
            left: 83.3%;
            &::after {
                animation-delay: 6s;
            }
        }
    }
}

@keyframes run {
    0% {
        top: -50%;
    }

    to {
        top: 110%;
    }
}


.breadcrumb {
    letter-spacing: 0.5px;
    text-align: center;

    .breadcrumb-item {
        text-transform: uppercase;
        font-size: 13px;
        font-weight: 600;

        &::before {
            content: "\F0142";
            font-size: 14px;
            font-family: "Material Design Icons";
            padding-left: 8px;
        }

        &:first-child {
            &::before {
                content: "";
            }
        }

        a {
            color: $gray-700;
            transition: all 0.5s ease;

            &:hover {
                color: $primary;
            }
        }
    }
}


/*****************dark-mode******************/

body[data-layout-mode='dark'] {
    .page-title-box {
        background-image: url('../images/home/page-title-dark.png');
        background-size: cover;
        background-position: top;
        background-color: $gray-dark-300;
    }
    .breadcrumb { 
        .breadcrumb-item { 
            a {
                color: $text-muted;
                &:hover {
                    color: $primary;
                }
            }
        }
    }
}