//
//select.scss
//

.choices__list--multiple .choices__item {
    background-color: $primary;
    border-color: $primary;
    margin-bottom: 0;
}

.choices__list--dropdown {
    z-index: 2;
    border-color: $gray-400;
    font-weight: 500;
    max-height: 330px;
    box-shadow: $box-shadow-sm;

    .choices__item--selectable {
        padding: 0;
        line-height: 43px;
        min-height: 44px;
        padding-left: 15px;
        padding-right: 29px;
        text-align: left;

        &::after {
            display: none;
        }

        &.is-highlighted {
            color: $primary;
            background-color: $light;
        }
    }
}

.choices__input {
    background-color: transparent;
    margin-bottom: 0;
}

.choices__inner {
    color: $body-color;
    background-color: $white;
    padding: 12px;
    font-size: 15px;
    min-height: 39px;
    border: 1px solid $gray-400;
    font-weight: 500;
    border-radius: $border-radius;

    &:focus {
        border: 1px solid $gray-400;
        border-radius: $border-radius;
    }
}

.choices[data-type*=select-one] {
    .choices__input {
        background-color: transparent;
        border: 1px solid $primary;
        margin: 10px;
        border-radius: 5px;
        width: 90%;
    }

    &::after {
        border: 0;
        left: auto;
        content: "\F0140";
        font-family: 'Material Design Icons';
        top: 30%;
        right: 28px;
    }

    &.is-open {

        .choices__list--dropdown,
        .choices__inner {
            box-shadow: $box-shadow-sm;
            border-color: $gray-400;
        }

        &::after {
            margin-top: -2.5px;
        }
    }
}

.is-focused .choices__inner,
.is-open .choices__inner {
    border: 1px solid $gray-400;
}

/*****************dark-mode******************/

body[data-layout-mode='dark'] {

    .choices__inner,
    .choices__list--dropdown,
    .choices[data-type*=select-one] .choices__input,
    .choices__input {
        background-color: $gray-dark-300;
        border-color: $gray-dark-400;
        color: $gray-100;

        &:focus {
            border-color: $gray-dark-400;
        }
    }

    .choices[data-type*=select-one] {
        &.is-open {

            .choices__list--dropdown,
            .choices__inner {
                border-color: $gray-dark-400;
                box-shadow: $box-shadow-sm-dark;
            }
        }
    }

    .choices__list--dropdown {
        .choices__item--selectable {
            &.is-highlighted {
                background-color: $gray-dark-400;
                color: $primary;
            }
        }
    }
}


.crypto-exchange {
    .choices {
        position: absolute;
        top: 7px;
        width: 88px;
        right: 7px;

        .choices__inner {
            padding: 4px;
        }
    }
}


.crypto-buy {
    .choices {
        position: absolute;
        top: 5px;
        width: 88px;
        right: 7px;

        .choices__inner {
            padding: 4px;
            border: none;
        }
    }
}