.bg-token {
    background-image: url('../images/other/bg-01.png');
    background-size: cover;
    background-position: center;
}


.token-sale-box {
    position: relative;
    background-image: linear-gradient(to right, rgba($light, 99), transparent);
    padding: 12px 22px;

    &:before {
        content: "";
        background-image: linear-gradient(to bottom, rgba($primary, 0.5), rgba($primary, 0.03));
        position: absolute;
        bottom: 0;
        top: 0;
        left: 0;
        width: 4px;
        height: 100%;
        border-radius: 5px;
    }
}

.token-sale-progress {
    box-shadow: rgba($gray-700, 0.2) 0px 7px 29px 0px;
    background: $white;
    height: 60px;
    overflow: visible;
    margin: 100px 0px;

    .progress-bar {
        margin: 5px 0 5px 5px;
        font-size: 15px;
        font-weight: 500;
        position: relative;
        overflow: visible;

        .progress-text {
            color: $dark !important;
            position: absolute;
            text-align: start;
        }

        &::after {
            content: "";
            position: absolute;
            height: 60px;
            border: 1px dashed $gray-400;
            bottom: 30px;
            left: 19.5%;
        }

        &::before {
            position: absolute;
            content: "";
            width: 16px;
            height: 16px;
            background-color: transparent;
            border: 4px solid $gray-400;
            bottom: 25px;
            left: 20%;
            transform: translateX(-60%) translateY(50%);
            border-radius: 50%;
            padding: 0;
            line-height: 1;
        }

        &:nth-child(1) {
            &::after {
                left: 18.8%;
            }

            .progress-text {
                bottom: 90px;
                left: 21px;
            }
        }

        &:nth-child(2) {
            .progress-text {
                bottom: 90px;
                left: 65px;
            }
        }

        &:nth-child(3) {
            margin: 5px;

            &::after {
                left: 19.5%;
            }

            .progress-text {
                bottom: 90px;
                left: 98px;
            }
        }
    }
}


.token-allocation-list {
    margin: 0;
    font-size: 16px;

    li {
        padding: 8px 0px;

        .sale-border {
            border-left: 5px solid;
            border-color: $primary;

            span {
                margin: 0 0 0 8px;
            }
        }
    }

    li {
        &:nth-child(2) {
            .sale-border {
                border-color: $success;
            }
        }
    }

    li {
        &:nth-child(3) {
            .sale-border {
                border-color: $warning;
            }
        }
    }

    li {
        &:nth-child(4) {
            .sale-border {
                border-color: $danger;
            }
        }
    }
}



.token-sale-progress {
    @media (max-width: 575.98px) {
        height: 200px;
        margin: 40px 0;
        flex-direction: column;

        .progress-bar {
            height: 100%;
            width: 100% !important;
            margin: 2px 0;

            &::after {
                display: none;
            }

            &:nth-child(3) {
                margin: 2px 0;
            }

            &::before {
                left: 5%;
                bottom: 28px;
            }

            .progress-text {
                display: none;
            }
        }
    }
}

.token-bonus-box {
    position: relative;

    &::before {
        content: "";
        border-top: 1px dashed rgba($gray-700, 0.2);
        position: absolute;
        width: 76.5%;
        left: 131px;
        top: 52px;


    }

    @media (max-width: 1199.98px) {
        &::before {
            left: 115px;
        }
    }

    @media (max-width: 991.98px) {
        &::before {
            left: 4px;
            border-top-color: transparent;
            border-left: 1px dashed rgba($gray-700, 0.2);
            width: 1px;
            height: 73%;
            top: 49px;
        }

        .token-bonus {
            margin-bottom: -15px;
        }
    }
}

.token-sale-details {
    margin-top: -125px;
    position: relative;
}

#countdown {
    li {
        text-transform: uppercase;
        margin: 10px;
        font-size: 14px;

        span {
            display: block;
            font-size: 1.5rem;
            width: 58px;
            height: 58px;
            line-height: 58px;
            border: 1px solid $gray-400;
            text-align: center;
            border-radius: 50%;
            margin-bottom: 8px;
        }
    }

    &.token-sale-index {
        li {
            span {
                width: 72px;
                height: 72px;
                line-height: 72px;
                font-size: 2rem;
                box-shadow: $box-shadow-lg;
                border-color: transparent;
                border-radius: 8px;
                margin-bottom: 13px;
            }
        }
    }
}



body[data-layout-mode='dark'] {
    .token-sale-box {
        background-image: linear-gradient(to right, rgba($gray-dark-300, 99), transparent);
    }

    #countdown {
        &.token-sale-index {
            li {
                span {
                    background-color: $gray-dark-300;
                    box-shadow: $box-shadow-lg-dark;
                }
            }
        }
    }
}