// dropdown

.navbar {
  .dropdown {
    .dropdown-menu {
      box-shadow: $box-shadow-lg;
      min-width: 15rem;
  
      .dropdown-item {
        font-size: 15.5px;
        font-weight: 500;
        color: $gray-700;
        background-color: $white;
        transition: all 0.5s ease;
        &:hover {
          color: $primary;
        }
      }
  
      &.dropdown-menu-md {
        min-width: 25;
      }
     
      &.dropdown-menu-lg {
        min-width: 36rem;
        @media (max-width: 767.98px) { 
          max-width: 35rem;
          min-width: 0px;
         }
      }
    }
  }
}

.dropdown-header {
  text-transform: uppercase;
  padding: 0px 0px 14px;
  font-size: 13px;
  letter-spacing: .08px;
  line-height: 1.55;
  font-weight: 500;
}

//scrollbar css

.dropdown-scroll,
.ui-elements-bar,
.choices__list {
  &::-webkit-scrollbar {
    -webkit-appearance: none;
  }

  &::-webkit-scrollbar:vertical {
    width: 10px;
  }

  &::-webkit-scrollbar:horizontal {
    height: 10px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgba($dark, 0.20);
    border-radius: 10px;
    border: 2px solid $gray-100;
  }

  &::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: $gray-100;
  }
}

@media (max-width: 991.98px) {
  .navbar-nav {
    .dropdown {
      .dropdown-menu {
        border: none;
        box-shadow: none;
        margin-top: 0;
      }
    }
  }
}

@media (max-width: 575.98px) {
  .dropdown {
    position: static;

    .dropdown-menu {
      left: 10px;
      right: 10px;
    }
  }
}

@media (min-width:992px) {
  .dropdown-menu {
    animation-name: DropDownSlide;
    animation-duration: .3s;
    animation-fill-mode: both;

    &.dropdown-menu-center {
      left: 50%;
      transform: translate(-50%, 0);
    }
  }
  .dropdown-hover:hover>.dropdown-menu {
    display: block;
  }
}

@keyframes DropDownSlide {
  100% {
    margin-top: -1px;
  }

  0% {
    margin-top: 8px;
  }
}

/*****************dark-mode******************/

body[data-layout-mode='dark'] {
  .dropdown {
    .dropdown-menu {
      background-color: $gray-dark-300;
      border-color: $gray-dark-400;
      color: $gray-100;
      box-shadow: $box-shadow-lg-dark;
      .dropdown-item {
        background-color: $gray-dark-300;
        color: $gray-100;

        &:hover {
          color: $primary;
        }
      }
    }
  }
  .dropdown-header { 
    color: $text-muted;
  }

  .dropdown-scroll,
  .ui-elements-bar,
  .choices__list {
    &::-webkit-scrollbar-thumb {
      background-color: $gray-dark-400;
      border: 2px solid $gray-dark-300;
    }

    &::-webkit-scrollbar-track {
      background-color: $gray-dark-200;
    }
  }
}