//
//cta.scss
//

:root {
    --marquee-width: 80vw;
    --marquee-height: 20vh;
    --marquee-elements-displayed: 10;
    --marquee-element-width: calc(var(--marquee-width) / var(--marquee-elements-displayed));
    --marquee-animation-duration: calc(var(--marquee-elements) * 3s);
}

.marquee {
    overflow: hidden;
    position: relative;
    &::before, &::after {
        position: absolute;
        top: 0;
        width: 10rem;
        height: 100%;
        content: "";
        z-index: 1;
    }
    &::before {
        left: 0;
        background: linear-gradient(to right, $white 0%, transparent 100%);
    }
    &::after {
        right: 0;
        background: linear-gradient(to left, $white 0%, transparent 100%);
    }
}

.marquee-content {
    height: 100%;
    display: flex;
    animation: scrolling var(--marquee-animation-duration) linear infinite;
}

@keyframes scrolling {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(calc(-1 * var(--marquee-element-width) * var(--marquee-elements)));
    }
}

.marquee-content li {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    width: var(--marquee-element-width);
    max-height: 100%;
    font-size: 35px;
    white-space: nowrap;

    &:nth-child(even) {
        margin-top: 150px;
    }

    i {
        height: 60px;
        width: 60px;
        line-height: 60px;
        text-align: center;
        border-radius: 50%;
    }
}


@media (max-width: 600px) {
    :root {
      --marquee-width: 100vw;
      --marquee-height: 16vh;
      --marquee-elements-displayed: 3;
    }
    .marquee:before, .marquee:after { width: 5rem; }
  }

/****************** subscribe css *****************/
.bg-subscribe {
    background: $light;
    padding: 60px 0;
    position: relative;
    margin-bottom: -40px;
    z-index: 2;
}

.subscribe-form {
    .form-control {
        position: relative;
        padding: 15px 85px 15px 20px;
    }

    .btn {
        position: absolute;
        right: 5px;
        top: 5px;
        padding: 10px 14px;
    }
}

// About css 
.buy-about-img{
    background: linear-gradient(213deg,#eff176 0%,$primary 100%),
    linear-gradient(48deg,#eabcbe 35%,#f6dcd1 calc(35% + 1px),#ff9d73 45%,#6afedc calc(45% + 1px),#b9dbd3 65%,#3c9d9c calc(65% + 1px),#68d5d3 100%);
    background-blend-mode: overlay,exclusion;
    padding: 25px;
    @media (min-width: 992px) { 
        transform: perspective(750px) rotateX(6deg) rotateY(-12deg);
    }
}


