//
// helper.scss
//

.row>* {
  position: relative;
}

/*******color Loop*******/
@each $color,
$value in $theme-colors {
  .bg-soft-#{$color} {
    background-color: rgba($value, 0.1) !important;

    .border {
      border-color: $value !important;
    }
  }
}

.section {
  padding-top: 95px;
  padding-bottom: 95px;
  position: relative;

  @media (max-width: 991.98px) {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.section-title {
  .title {
    font-size: 36px;
    line-height: 1.3;
  }

  .desc-content {
    font-size: 19px;
  }
}

.para-desc {
  max-width: 570px;
}

.sub-title {
  font-size: 13px;
  font-weight: 600;
  letter-spacing: 0.3px;
  display: inline-block;
  padding: 7px 9px;
  border-radius: 40px;
}

::selection {
  color: $white;
  background: $primary;
}

/*******font-size*******/
.fs-11 {
  font-size: 11px;
}

.fs-12 {
  font-size: 12px;
}

.fs-13 {
  font-size: 13px;
}

.fs-14 {
  font-size: 14px;
}

.fs-15 {
  font-size: 15px;
}

.fs-16 {
  font-size: 16px;
}

.fs-17 {
  font-size: 17px;
}

.fs-18 {
  font-size: 18px;
}

.fs-19 {
  font-size: 19px;
}

.fs-20 {
  font-size: 20px;
}

.fs-21 {
  font-size: 21px;
}

.fs-22 {
  font-size: 22px;
}

/*******font-weight*******/
.fw-medium {
  font-weight: $font-weight-medium;
}

.fw-semibold {
  font-weight: $font-weight-semibold;
}

.bg-overlay {
  background-color: rgba($dark, 0.50);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
}

.rounded-4 {
  border-radius: 0.938rem;
}

.bg-gradient-light {
  background-image: linear-gradient(to bottom, #FAFAFC, $white);
}

/*************Avtar size*************/
.avatar-xs {
  height: 2.5rem;
  width: 2.5rem;
}

.avatar-sm {
  height: 3rem;
  width: 3rem;
}

.avatar-md {
  height: 4rem;
  width: 4rem;
}

.avatar-lg {
  height: 5rem;
  width: 5rem;
}

.avatar-title {
  align-items: center;
  display: flex;
  font-weight: 500;
  height: 100%;
  justify-content: center;
  width: 100%
}

/*******link color*******/
.primary-link {
  color: $body-color;
  transition: all 0.5s ease;

  &:hover {
    color: $primary !important;
  }
}

.alert-dismissible {
  .btn-close {
    &:focus {
      box-shadow: none;
    }
  }
}

#back-to-top {
  width: 35px;
  height: 35px;
  position: fixed;
  bottom: 20px;
  right: 30px;
  background: lighten($primary, 5%);
  border-color: transparent;
  border-radius: 5px;
  color: $white;
  transition: all 0.5s ease;
  opacity: 0;
  z-index: 999;
  &:hover {
    border-radius: 5px;
    background-color: $primary;
  }
}

.form-control,
.form-select {
  &::-webkit-input-placeholder {
    color: $gray-500;
    font-size: 15px;
  }

  &::-moz-placeholder {
    /* Firefox 19+ */
    color: $gray-500;
    font-size: 15px;
  }

  &:-ms-input-placeholder {
    /* IE 10+ */
    color: $gray-500;
    font-size: 15px;
  }

  &:-moz-placeholder {
    /* Firefox 18- */
    color: $gray-500;
    font-size: 15px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px !important;
  }

  .custom-container {
    max-width: 90%;
  }
}


body[data-layout-mode='dark'] {
  .primary-link {
    color: $white;
    transition: all 0.5 ease;

    &:hover {
      color: $primary;
    }
  }
}