.faqs {
    background-color: transparent;
    .accordion-item {
        .accordion-header {
            .accordion-button {
                font-weight: $font-weight-medium;

                &:focus {
                    box-shadow: none;
                }

                &::after {
                    content: "\F0143";
                    font-family: "Material Design Icons";
                    background: none;
                    height: 30px;
                    width: 30px;
                    border-radius: 50%;
                    color: $dark;
                    font-size: 19px;
                    align-items: center;
                    display: flex;
                    justify-content: center;
                }

                &:not(.collapsed) {
                    box-shadow: none;
                }
            }
        }
    }
}


.faq-menu {
    .nav-link {
        color: $text-muted;
        font-weight: 500;
        text-align: left;
        padding: 2px 20px;
        border-left: 2px solid transparent;
        border-radius: 0px;
        margin-bottom: 11px;
        &.active {
            background-color: transparent;
            color: $primary;
            border-left-color: $primary;
        }
    }

    
}

.accordion-item {
    .accordion-header {
        .accordion-button {
            padding: 15px 21px;
            background-color: $white;
            font-weight: $font-weight-medium;
            &::after {
                content: "\F0415";
                font-family: "Material Design Icons";
                background: none;
                height: 30px;
                width: 30px;
                border-radius: 50%;
                color: $dark;
                font-size: 19px;
                align-items: center;
                display: flex;
                justify-content: center;
            }
            &:not(.collapsed) {
                box-shadow: none;
                border-bottom: 1px solid;
                border-bottom-color: $border-color;
                &::after {
                    content: "\F0374";
                }
            }
        }
    }

    .accordion-body {
        background-color: $white;
    }
}

body[data-layout-mode='dark'] {
    .accordion-item {
        border-color: $gray-dark-400;
        .accordion-header {
            .accordion-button { 
                background-color: $gray-dark-300;
                border-bottom-color: $gray-dark-400;
                border-top:1px solid $gray-dark-400;
                color: $gray-dark-800;
                &::after { 
                    color: $primary;
                }
            }
        }
        .accordion-body {
            background-color: $gray-dark-300;
        }
    }
}