//
// Loader
//

#preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: linear-gradient(to right, $primary, darken($primary, 5%));
    z-index: 99999;
    transition: all 0.5s ease;
    visibility: visible;
    opacity: 1;

    #status {
        position: absolute;
        left: 0;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
    }

    #bitcoin {
        padding-top: 80px;
        width: 200px;
        margin: 0 auto;
        display: block;
        text-align: center;

        .path {
            stroke-dasharray: 2110;
            stroke-dashoffset: 2110;
            stroke-width: 2;
            stroke-linecap: round;
            animation: preloader 6s linear infinite;
            fill-opacity: 0;
            stroke: $white;
        }
    }
}

@keyframes preloader {
    0% {
        stroke-dashoffset: 2110;
        opacity: 0;
        stroke: $white;
    }

    15% {
        opacity: 1;
        stroke: $white;
    }

    70% {
        opacity: 1;
        stroke: $white;
    }

    100% {
        stroke-dashoffset: 0;
        opacity: 0;
        stroke: darken($white, 2.4%);
    }
}