//
//general.scss
//

.form-floating>label {
    font-size: 15px;
}

.crypto-exchange-form {
    .form-control {
        border-color: $gray-400;
        box-shadow: $box-shadow;
    }
}

.check-list {
    height: 26px;
    width: 26px;
    line-height: 26px;
    text-align: center;
    background-color: rgba($success, 0.55);
}

.about-list-box {
    padding: 10px;
    border-radius: 40px;
    background-image: linear-gradient(to right, rgba($success, 0.08), transparent);
}


/*****************dark-mode******************/

body[data-layout-mode='dark'] {
    color: $gray-dark-900;
    background-color: $gray-dark-100;
    
    .bg-light {
        background-color: $gray-dark-300 !important;
    }

    .text-dark {
        color: $gray-200 !important;
    }

    .bg-gradient-light {
        background-image: linear-gradient(to bottom, $gray-dark-200, $gray-dark-100);
    }

    .card,
    .card-header {
        background-color: $gray-dark-200;
        border-color: $gray-dark-300;
    }

    .shadow {
        box-shadow: $box-shadow-dark;
    }

    .shadow-sm {
        box-shadow: $box-shadow-sm-dark;
    }

    .shadow-lg {
        box-shadow: $box-shadow-lg-dark;
    }

    .bg-white {
        background-color: $gray-dark-200 !important;
    }

    .form-control {
        color: $gray-dark-700;
        background-color: $gray-dark-300;
        border-color: $gray-dark-300
    }

    .text-secondary {
        color: $text-muted !important;
    }

    .border,
    .border-top,
    .border-bottom,
    .border-start,
    .border-end {
        border-color: lighten($gray-dark-300, 2.5%) !important;
    }

    .modal-content {
        background-color: $gray-dark-100;
        border-color: $gray-dark-300;
    }
}


body[data-layout-mode='dark']{
    &.bg-light {
        color: $gray-dark-900;
        background-color: $gray-dark-100 !important;
    }
}