.pricing-page-title {
    padding-bottom: 232px;
    clip-path: polygon(100% 0, 100% 48%, 87% 86%, 0 100%, 0 0);
}

.bg-pricing {
    margin-top: -155px;
    z-index: 1;
}

.pricing-box {
    position: relative;
    overflow: hidden;
    &::before {
        content: "";
        background-color: rgba($light, 0.80);
        position: absolute;
        width: 100%;
        top: -45px;
        left: -31px;
        height: 164px;
        border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
        transform: rotate(353deg);
        z-index: 0;
    }
    .card-body {
        z-index: 1;
    }
}

.ribbon {
    position: relative;
    display: inline-block;
    z-index: 1;
}


.ribbon {
    position: absolute;
    right: 0;
    transform-origin: 0px 0px;
    transform: translate(50%, -28.5px) rotate(45deg);
}

.ribbon__bends {
    color: darken($primary, 15%);
}

.ribbon__body {
    color: $primary;
}

.ribbon__text {
    color: $white;
    position: absolute;
    top: 5px;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: center;
    text-transform: uppercase;
    font-size: 11px;
    line-height: 1;
    font-weight: $font-weight-medium;
}


.ribbon--css {
    border-bottom: 20px solid $primary;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    height: 20px;
    width: 140px;
    text-transform: uppercase;
    letter-spacing: -0.05em;
    font-size: 12px;
    white-space: nowrap;
    text-align: center;
    font-weight: bold;

    &:before,
    &:after {
        top: 20px;
        content: ' ';
        position: absolute;
        width: 0;
        height: 0;
    }
}

.ribbon--with-squares {
    border: 0;
    height: auto;
    width: auto;

    .ribbon__inner {
        border-bottom: 20px solid $primary;
        border-left: 20px solid transparent;
        border-right: 20px solid transparent;
        height: 0;
        width: 140px;
        text-transform: uppercase;
        letter-spacing: -0.05em;
        font-size: 12px;
        white-space: nowrap;
        line-height: 23px;
        text-align: center;
        font-weight: bold;
        display: block;
        position: relative;
        z-index: 1;
    }

    &:before,
    &:after {
        width: 20px * 2 / 3;
        height: 20px * 2 / 3;
        background-color: $primary;
    }

    &:before {
        left: 0;
        transform-origin: top left;
        transform: rotate(-45deg);
    }

    &:after {
        right: 0;
        transform-origin: top right;
        transform: rotate(45deg);
    }
}

.ribbon--triangle {
    line-height: 20px;

    &:before {
        left: -20px;
        border-style: solid;
        border-width: 20px/2 20px/2 0 20px/2;
        border-color: $primary transparent transparent transparent;
    }

    &:after {
        right: -20px;
        border-style: solid;
        border-width: 20px/2 20px/2 0 20px/2;
        border-color: $primary transparent transparent transparent;
    }
}

body[data-layout-mode='dark'] {
    .pricing-box {
        &::before {
            background-color: rgba($gray-dark-300, 0.65);
        }
    }
}