//process.scss


.process-box {
    position: relative;
    &::before {
        content: "";
        position: absolute;
        left: 67%;
        top: 36px;
        width: 195px;
        height: 36px;
        background-image: url('../images/line2.png');
        background-position: center;
        opacity: 0.1;
    }
    @media (max-width: 991.98px) { 
        &::before {
            background-image: none;
            width: 0px;
        }
    }
}