@each $color,
$value in $theme-colors {
    .btn-soft-#{$color} {
        background-color: rgba($value, 0.15) !important;
        border-color: transparent;
        color: #{$value} !important;

        &:hover,
        &:focus,
        &:active,
        &.active,
        &.focus,
        &:not(:disabled):not(.disabled):active,
        &:not(:disabled):not(.disabled):active:focus,
        .open>.dropdown-toggle.btn-primary {
            color: $white !important;
            background: rgba($value, 1) !important;
        }
    }
}

.btn-hover {
    transition: all 0.5s ease;
    &:hover {
        transform: translateY(-6px);
    }
}

.btn {

    &.btn-info,
    &.btn-purple {
        color: $white;
    }
}

.btn {

    &.btn-outline-white,
    .btn-outline-light {

        &:focus,
        &:active,
        &:hover,
        &.active {
            color: $dark;
        }
    }

    &.btn-outline-primary,
    &.btn-outline-dark,
    &.btn-outline-success {

        &:active,
        &:hover,
        &.active {
            color: $white !important;
        }
    }
}


body[data-layout-mode='dark'] {

    // buttons 
    .btn-light {
        color: $white;
        background-color: $gray-dark-300;
        border-color: $gray-dark-300 !important;
    }

    .btn-outline-light{
        color: $gray-dark-500;
        border-color: $gray-dark-300;
        &:hover{
            color: $gray-dark-500;
            background-color: $gray-dark-300;
            border-color: $gray-dark-300;
        }
    }

    .btn-check:focus+.btn-light, 
    .btn-light:focus,
    .btn-check:focus+.btn-dark, 
    .btn-dark:focus,
    .btn-check:focus+.btn-outline-light, 
    .btn-outline-light:focus{
        box-shadow: 0 0 0 $btn-focus-width rgba($gray-dark-300, .5);
    }

    .btn-soft-dark, .btn-outline-dark{
        color: $gray-dark-600;
    }

    // buttons 
    .btn-dark {
        background-color: lighten($gray-dark-200,2%);
        border-color: lighten($gray-dark-200,2%);
    }

    .btn-outline-dark{
        border-color: lighten($gray-dark-200,2%);
        &:hover{
            background-color: lighten($gray-dark-200,2%);
            border-color: lighten($gray-dark-200,2%);
        }
    }

    .btn-soft-dark{
        background-color: rgba($gray-dark-200,0.25);
        border-color: rgba($gray-dark-200,0.25);
        &:hover{
            background-color: lighten($gray-dark-200,2%);
            border-color: lighten($gray-dark-200,2%);
        }
    }
}