
.features-tabs {
    .nav-pills {
        padding: 8px;
        background-color: $white;
        box-shadow: $box-shadow;
        .nav-link{
            border-radius: 10px;
            color: $body-color;
            .avatar-sm {
                i {
                    font-size: 20px;
                    line-height: 48px;
                }
            }
            &.active {
                background-color: $primary;
                color: $white;
            }
        }
    }
}

.nav-tab-content {
    .tab-pane-image {
        background-color: $white;
        border-radius: 10px;
        padding: 15px;
        box-shadow: $box-shadow;
    }
}

.features-box{
    .features-img {
        height: 210px;
    }
    .features-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        background: linear-gradient(45deg, transparent, rgba($primary, 0.2))
    }
}




.bitcoin-icon {
    position: absolute;
    top: 15px;
    right: 0px;
    opacity: 0.08;
    animation: rotating alternate ease-in-out infinite 20s;
}


.crypto-chart {
    overflow: hidden;
    position: absolute;
    left: 24px;
    right: 24px;
    bottom: 24px;
    opacity: 0.15;
    z-index: 0;
}

.apexcharts-gridline {
    pointer-events: none;
    stroke: $light;
  }

.bounce {
    height: 50px;
    overflow: hidden;
    position: relative;
    background: #fefefe;
    color: #333;
    border: 1px solid #4a4a4a;
    ul {
        li{
            text-align: center;
            transform: translateX(50%);
            animation: bouncing-text 10s linear infinite alternate;
        }
    }
}

@keyframes bouncing-text {
    0% {
        transform: translateX(50%);
    }
    100% {
        transform: translateX(-50%);
    }
}

//Benefits

.benefits-content{
    position: absolute;
    max-width: 318px;
    bottom: 33px;
    background: $white;
    border-radius: 26px;
    left: 0;
}


.bg-service-cta{
    background-image: url('../images/features/bg.jpg');
    background-size: cover;
    background-position: center;
    padding: 118px 0 220px 0;
    position: relative;
    .bg-overlay {
        background-color: rgba($footer-bg, 0.85);
    }
    .intro-play {
        &::before {
            content: "";
            position: absolute;
            z-index: 0;
            left: 50%;
            top: 24px;
            width: 52px;
            height: 52px;
            background: $primary;
            border-radius: 50%;
            animation: pulse-border 1500ms ease-out infinite;
        }
    }
    @media (max-width: 991.98px) { 
        padding: 118px 0 120px 0;
     }
}

@keyframes pulse-border {
    0% {
      transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1);
      opacity: 1;
    }
    100% {
      transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.5);
      opacity: 0;
    }
  }
  

body[data-layout-mode='dark'] {
    .features-tabs {
        .nav-pills { 
            background-color: $gray-dark-300;
            box-shadow: $box-shadow-dark; 
            .nav-link {
                color: $gray-dark-700;
            }
        }
    }
    .nav-tab-content {
        .tab-pane-image { 
            background-color: $gray-dark-300;
            box-shadow: $box-shadow-dark; 
        }
    }
}