//
//home.scss
//

#home {
    position: relative;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}

.bg-home {
    padding: 195px 0 30px 0;
    position: relative;
    @media (max-width: 767.98px) {
        padding: 170px 0 60px 0;
    }
}

.watch-video {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    a {
        display: inline-block;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.home-icon-list {
    li {
        position: absolute;
        font-size: 35px;

        &:first-child {
            top: 34%;
            left: 22%;
            animation: rotating5 alternate ease-in-out infinite 14s;
        }
        
        &:nth-child(2) {
            top: 50%;
            right: 20%;
            animation: rotating alternate ease-in-out infinite 10s;
        }

        &:nth-child(3) {
            bottom: 15%;
            right: 6%;
            animation: rotating4 alternate ease-in-out infinite 10s;
        }

        &:nth-child(4) {
            bottom: 10%;
            left: 13%;
            animation: rotating alternate ease-in-out infinite 10s;
        }

        &:nth-child(5) {
            top: 20%;
            right: 20%;
            animation: rotating2 alternate ease-in-out infinite 10s;
        }

        &:nth-child(6) {
            top: 12%;
            left: 10%;
            animation: rotating3 alternate ease-in-out infinite 10s;
        }

        &:nth-child(7) {
            bottom: 35%;
            left: 10%;
        }
    }
}

@keyframes rotating {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes rotating2 {
    0% {
        transform: rotate(0deg);
        right: 10%;
    }

    100% {
        transform: rotate(360deg);
        right: 20%;
    }
}

@keyframes rotating3 {
    0% {
        top: 15%;
    }

    100% {
        top: 30%;
    }
}

@keyframes rotating4 {
    0% {
        bottom: 10%;
    }

    100% {
        bottom: 30%;
    }
}

@keyframes rotating5 {
    0% {
        left: 20%;
    }

    100% {
        left: 30%;
    }
}


//home-2

.bg-home2 {
    padding: 245px 0 0px;
    overflow-x: clip;
    background-position: bottom;
    @media (max-width: 991.98px) { 
        padding: 160px 0 55px 0;
        .home-img{
            max-width: 100%;
            height: auto;
        }
    }  
}

.homeSlider {
    height: 150px;
    @media (min-width: 768px) and (max-width: 991.98px) { 
        height: 320px;
    }
    @media (max-width: 767.98px) { 
        height: 580px;
     }
}


//home-3

.bg-ico {
    position: relative;
    padding: 210px 0 65px 0;
    margin-bottom: 4px;
}

.home-ico-card {
    background-color: darken($primary, 2.5%);
    z-index: 2;
}

.bg-home3{
    position: relative;
    padding: 240px 0 190px 0;
    background-image: url('../images/home/bg-home-03.jpg');
    background-position: center;
    background-size: cover; 
    .bg-overlay {
        background:linear-gradient(to right,$primary,#5253c2f2) ;
    }
}

.buy-sell-list{
    position: relative;
    margin-top: -145px;
}

.bg-payment{
    position: relative;
    padding: 195px 0 105px 0;
    @media (max-width: 991.98px) { 
        padding: 150px 0 70px 0;
    }
}

@media (max-width: 991.98px) { 
    .home-img{
        max-width: 100%;
        height: auto;
    }
}

/****************Wallets Home*****************/

.bg-wallet{
    position: relative;
    padding: 170px 0 80px 0;
    .bg-overlay {
        background-color: rgba($primary, 0.01);
        background-image: url('../images/home/bg-wallet.png');
        background-size: cover;
        background-position: center;
        opacity: 0.08;
    }
}


body[data-layout-mode='dark'] {
    background-image: url('../images/features/01.png');
}