
.bg-auth {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 60px 0px;
    background-color: $light;
}

.auth-form {
    background-color: rgba($white, 0.08);
    border: none;
    backdrop-filter: blur(8px);
    .form-control {
        background-color: rgba($white, 0.10);
        border: none;
        color: $white;
        &::-webkit-input-placeholder {
            color: rgba($white, 0.30);
            font-size: 15px;
          }
        
          &::-moz-placeholder {
            /* Firefox 19+ */
            color: rgba($white, 0.30);
            font-size: 15px;
          }
        
          &:-ms-input-placeholder {
            /* IE 10+ */
            color: rgba($white, 0.30);
            font-size: 15px;
          }
        
          &:-moz-placeholder {
            /* Firefox 18- */
            color: rgba($white, 0.30);
            font-size: 15px;
          }
    }
    .nav-pills {
        .nav-link {
            color: $white;
            padding: 8px 30px;
            &.active {
                background-color: rgba($white, 0.10);
                font-weight: $font-weight-medium;
            }   
        }
    }
    .iti--allow-dropdown  {
        color: $body-color;
        display: block;
    }
}

.password-reset-box {
    position: relative;
    background-color: rgba($white, 0.10);
    padding: 16px;
}

.verification-code-form {
    .form-control {
        height: 60px;
        width: 60px;
    }
}