//
//footer.scss
//

.bg-footer {
    background: $footer-bg;
    padding: 95px 0;
    position: relative;
    margin-top: -1px;
    &::after {
        content: "\ed60";
        font-family: cryptofont;
        position: absolute;
        right: 0;
        top: 0;
        color: rgba($white, 0.05);
        transform: scale(1.5) rotate(45deg);
        font-size: 180px;
    }
}

.footer-dark-logo {
    display: none;
}
.footer-light-logo {
    display: block;
}

.footer-social-menu {
    li {
        a {
            display: inline-block;
            width: 32px;
            height: 32px;
            line-height: 32px;
            text-align: center;
            color: rgba($white, 0.50);
            background-color: rgba($white, 0.08);
            border-radius: 40px;
            transition: all 0.5s ease;
            &:hover {
                background-color: $white;
                color: $primary;
                border-color: $white;
            }
        }
    }
}

.footer-content{
    color: rgba($white, 0.50);
}

.footer-header{
    font-size: 12.5px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.8px;
    color: $white;
}

.footer-list {
    li {
        a {
            display: inline-block;
            margin-bottom: 0.75rem;
            color: rgba($white, 0.50);
            transition: all 0.5s ease;
            &:hover {
                color: $white;
            }
        }
    }
}

.footer-alt-list{
    li {
        a {
            display: inline-block;
            color: rgba($white, 0.50);
            transition: all 0.5s ease;
            &:hover {
                color: $white;
            }
        }
    }
}

.footer-alt{
    background: $footer-bg;
    color: rgba($white, 0.50);
    padding: 20px 0px;
    border-top: 1px solid;
    border-color: lighten($footer-bg, 3%);
}

.footer-alt-list {
    li {
        border-right: 1px solid rgba($white, 0.45);
        padding-right: 10px;
        &:last-child {
            border: none;
            padding-right: 0px;
        }
    }
}

.footer-shape, .shape {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    pointer-events: none;
    &>svg {
        width: 100%;
        height: auto;
    }
}


body[data-footer='light'] {
    .bg-footer { 
        background-color: $gray-300;
        .footer-content{
            color: $text-muted;
        }
        
        .footer-social-menu {
            li {
                a {
                    color: $text-muted;
                    &:hover {
                        color: $primary;
                    }
                }
            }
        }
        .footer-list {
            li {
                a {
                    color: $text-muted;
                    &:hover {
                        color: $primary;
                    }
                }
            }
        }
        .footer-header{
            color: $gray-700;
        }
        .footer-dark-logo {
            display: block;
        }
        .footer-light-logo {
            display: none;
        }
    }
    .footer-alt{
        background-color: $gray-300; 
        border-color: darken($gray-300, 5%);
        color: $text-muted;
    }
    .footer-shape {
        &>svg{
            path {
                fill: $gray-300;
            }
        }
    }
}

body[data-layout-mode='dark'] {
    .bg-footer { 
        background-color: $footer-bg-dark;
        .footer-content{
            color: $text-muted;
        }
        .footer-header{ 
            color: $gray-dark-800;
        }
        .footer-social-menu {
            li {
                a {
                    color: $text-muted;
                    &:hover {
                        color: $primary;
                    }
                }
            }
        }
    }
    .footer-alt{
        background-color: $footer-bg-dark; 
        border-color: lighten($footer-bg-dark, 2.5%);
        color: $text-muted;
    }
    .footer-list {
        li {
            a {
                color: $text-muted;
                transition: all 0.5s ease;
                &:hover {
                    color: $primary;
                }
            }
        }
    }
    .footer-shape {
        &>svg{
            path {
                fill: $footer-bg-dark;
            }
        }
    }
    .shape {
        &>svg{
            path { 
                fill: $gray-dark-100;
            }
        }
    }
}  