.counter {
    position: relative;
    display: flex;
    overflow: hidden;
    justify-content: center;
    height: 2.4rem;
    font-size: 2.4rem;
    font-weight: 600;
    &>span {
        z-index: 1;
        display: flex;
        flex-direction: column;
        height: 100%;
        transition: transform 2s ease;
        transform: translateY(0);
        line-height: 1;
        span {
            flex: 0 0 100%;
            height: 100%;
        }
    }
  }
  