.blog-box {
    transition: all 0.5s ease;
    &:hover {
        transform: translateY(-10px);
    }
}


.blog-item{
    img {
        min-height: 295px;
        width: 100%;
        object-fit: cover;
        transition: all 0.5s ease;
    }
    &:hover {
        img {
            transform: scale(1.2);
        }
    }
}

.blog-search{
    position: relative;
    z-index: 2;
    margin-top: -36px;
    .form-control {
        padding: 0.75rem 8.75rem 0.75rem 1.35rem;
    }
    .btn {
        position: absolute;
        right: 10px;
        top: 10px;
        padding: 10px 14px;
    }
}

.bg-blog {
    padding-top: 165px;
}

.blog-details-page{
    padding-top: 180px;
    padding-bottom: 70px;
}

.blog-details {
    .blog-details-img {
        width: 100%;
        height: 526px;
        object-fit: cover;
    }
}

.blog-social-list{
    a {
        display: inline-block;
        color: $text-muted;
        transition: all 0.5s ease;
        &:hover {
            color: $primary;
        }
    }
}

.blogGridSlider {
    img {
        clip-path: ellipse(100% 100% at -1% 50%);
    }
    @media (max-width: 768.98px) { 
        img {
            clip-path: ellipse(100% 73% at 50% 26%);
        }
    }
    .swiper-slide {
        padding: 24px;
    }
}

.blog-grid-box {
    img {
        clip-path: ellipse(100% 73% at 50% 26%);
    }
    transition: all 0.5s ease;
    &:hover {
        transform: translateY(-10px);
    }
}
    