.team-box {
    background-image: linear-gradient(1deg, $white 0%,rgba($primary, 0.30) 100%);
    .team-social {
        position: absolute;
        bottom: 10px;
        right: 0;
        left: 0;
        li {
            transform: translateY(40px);
            transition: 0.5s;
            opacity: 0;
            transition-delay: calc(0.2s * var(--i));
            a {
                color: $primary;
                font-size: 20px;
                height: 32px;
                width: 32px;
                line-height: 32px;
                border-radius: 5px;
                display: inline-block;
                background-color: $white;
              }
          }
    }
    &:hover {
        .team-social {
            li {
                transform: translateY(0);
                opacity: 1;
              }
        }
    }
}

.quote-icon {
    position: absolute;
    font-size: 42px;
    top: -30px;
    left: 17px;
    opacity: 0.2;
}

.testi-box{
    position: relative;
    &::before {
        content: "\ec51";
        font-family: 'remixicon';
        font-size: 89px;
        top: -24px;    
        position: absolute;
        color: rgba($primary, 0.06);
        right: 7px;
    }
}

.swiper-pagination {
    .swiper-pagination-bullet{
        &.swiper-pagination-bullet-active-main{
            background-color: $primary;
            padding: 4px;
        }
    }
}


body[data-layout-mode='dark'] {
    .team-box {
        background-image: linear-gradient(1deg, $gray-dark-100 0%,rgba($primary, 0.30) 100%);
    }
}